.searchSelectBox{
    color: #666;
    background: white;
    height: 60px;
    border: 1px solid #999999;
    box-shadow: none !important;
    width: 76%;
    border-radius: 5px 0 0 5px;
    text-align: left;
    padding-left: 25px;
    padding-right: 15px;
    width: 50%;
    font-family: sans-serif;
    font-weight: 500;
    outline: none;
    padding-top: 15px;
    padding-bottom: 15px;
}
.searchBtn{
    background: #795ea1;
    border-radius: 0 5px 5px 0;
    color: #fff;
    padding: 9px 20px;
    color: #fff;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    border: none;
    height: 60px;
    outline: none;
}
.searchWrapper{
    display: flex;
    padding-top: 12px;
}
.optionLabels{
    font-weight: 500;
    font-family: sans-serif;
}
.searchGrid{
    top:24px;
    position:absolute;
}