.sectionContainer{
    background: red;
    width: 100%;
}
.tagline{
    margin:0px 12px 0px 12px;
}
h3{
    color: white;
    font-weight: bold;
    position: relative;
    margin:0px;
}
@keyframes right_to_left {
    from {left: 0px;}
  to {left:300px;}
  }
.subSectionSearch{
    display: flex;
    margin:0px 12px 0px 12px;
    height:10%;
    align-items: center;
    justify-content: space-between;
    font-size:20pt;
    font-weight: bold;
}
.favContainer{
    margin: 12px 100px 12px 100px;
}
.favContent{
    display: flex;
    margin: 0px 12px 0px 12px;
}
.favBox{
    display: flex;
    flex-direction: column;
    padding: 0px 12px 0px 12px;
    align-items: center;
    box-shadow: 0px 0px 4px #ccc;
    height: 326px;
    width: 271px;
}
.favText{
    text-align: center;
    color: #404040;
    font-size: 40px;
}
.favWrapper{
    display: flex;
    margin-bottom: 40px;
}
.favBtnWrapper{
    margin: 12px 0px 12px 0px;
   
}
.favBtn{
    border: 1px solid transparent;
    background-color: #343E7E;
    color:#ffff;
    border-radius: 5px;
    border:1px solid transparent;
    width: 240px;
    height: 54px;
    font-size:16px;
    font-family: Arial;
    cursor: pointer;
    outline: none;
    font-weight: bold;
}
.favImage{
    height: 236px;
    width: 250px;
}
.backgroundImageWrapper{
position: relative;
}
.backgroundImage{
    width:100%;
    height:450px;
}