
.productItem{
    display: flex;
    background: white;
}


.productCarouselContentWrapper{
    height: 338px;
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid #e8e8e8;
    border-right: 1px solid #eeeeee;
    align-items: center;
}
.productCarouselImage{
    padding: 10px 10px 0px 10px;
    height: 250px;
    width: 200px;
}
.productCarouselLabelWrapper{
    display: flex;
    justify-content: center;
margin-bottom: 10px;   
    flex-direction: column;
    align-items: center;
}
.itemDesc{
    padding: 0 10px;
    margin-top: 5px;
    font-size: 14px;
    color: #333;
    font-weight: normal;
}
.productItem1:hover{
    box-shadow: inset 0px 0px 19px 2px #e8e8e8;
    cursor: pointer;
}