.header{
  border: 1px solid #f5f2f2;
}
.logo{   
  width: 114px;
  height: 67PX;}
.footer{
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
  color:#795ea1;
  background: #f8f8f8;}
.content1{
  background: blueviolet;
}
.baseContainer{
  display: flex;
  flex-direction: column;
  height: 100vh;
}
.section{
  display: flex;
  flex: 1;
  flex-direction: column;
  background-color: #fafafa;
}
.headerContainer{
  margin: 0px 12px 0px 12px;
  display: flex;
  justify-content: space-between;
  padding-top: 0.5rem;
    padding-bottom: 0.5rem;
}
.searchInput{
  border-radius: 22px;
  padding: 4px;
  height:30px;
  width:500px;
  border:1px solid grey;
  outline:none;
}
.searchContainer{
  display: flex;
    align-items: center;
    position:relative;
}
.searchIcon{
  position: absolute;
  right:5px;
}

.headerRightWrapper{
  display: flex;
    flex-direction: column;
}
.headerRightSubDiv{
  display: flex;
}
.headerContact{
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.headerLabel{
  font-weight: 600;
    color: #9a89bb;
    padding: 8px 11px;
    font-size: 15px;
    cursor: pointer;
  font-family: sans-serif;

}
.signUp{
  color: #9a89bb;
  text-decoration: none;
}
.signUp:hover{
  text-decoration: none;
}
.headerLabel:hover{
  color: #65518d;
}
.headerServices{
  display: flex;
}
.contactNo{
  padding-left: 12px;
  color: #795ea1;
  font-size: 17px;
  font-weight: 800;
}
.contactImage{
  font-size: 22px;
  color: #795ea1;
}
.footerContent{
  padding: 3px 5px;
  font-size: 12pt;
  cursor: pointer;
}
.copyRight{
  height: 12px;
}