
.serviceBase{
    padding-top:12px;
    position: absolute;
    top:240px;
}
.servicesWrapper{
    background: white;
    padding: 12px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
}
.serviceContent{
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}
.servicesLabel{
    font-weight: 600;
    margin-bottom: 8px;
    font-family: 'Heebo', sans-serif;
    font-size: 15px;
    color: #707070;
}
.serviceLogo{
    width: 25px;
    height:25px;
    object-fit: cover;
    margin-right:2px;
}
.boxContainer{
    font-weight: 500;
    border-radius: 2px;
    width: 15.68%;
    text-align: center;
    height: 50px;
    object-fit: scale-down;
    margin: 0 0 0 0;
    border: 1px solid #644b8e;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s ease-in-out;
    /* padding: 9px 3px; */
    font-size: 15px;
    color: #644b8e;
    cursor:pointer;
}
.activeService{
    background: #644b8e;
    color:white;
}
.boxContainer:hover{
    background: #644b8e;
    color:white;
}