.carouselContentWrapper{
    width: 350px;
	margin: 16px;
	background-clip: border-box;
    border: 1px solid rgba(0,0,0,.125);
	border-radius: .25rem;
	background: white;
}
.carouselLabelWrapper{
	display: flex;
    justify-content: center;
	margin: 10px;
	font-weight: bold;
	color: #484848;
	font-size: 18px;
}
.item1{
	display: flex;
}
.arrow{
	background-image: none !important;
}
.arrow:hover{
	background-image: none;
}
.carousel-indicators{
	margin-bottom: 42px 
}
.carouselImage{   width: 350px;
border-top-right-radius: .25rem;
border-top-left-radius: .25rem;
    max-height: 180px;
    object-fit: cover;}