.wrapper{
    display: flex;
    justify-content: space-between;
    height:440px;
}
.whyUsBox{
    display: flex;
    flex: 1 1;
    background: white;
    justify-content: center;
    margin: 16px;
}
.tick{
    height:30px;
    width:30px;
}
.whyUsContent{
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #795ea1;;
    color:white;
    font-size: 1.6rem;
    font-weight: bold;
}
ul{
    list-style-type: none;
}
.titleWhyUs{
    font-weight: bold;
    padding:12px;
    font-size: 20px;
}
.whyUsDesc{
    padding:8px 12px 8px 12px;
}