.cateogryWrapper{
    display: flex;
    justify-content: space-between;
}
.boxWrapper{
    background: #fff;
    height: 100%;
    padding-top: 20px;
    width: 350px;
  box-shadow: inset 0px 0px 19px 2px #e8e8e8;

}
.boxWrapper:hover{
    box-shadow: 0px 0px 8px 2px #000000;
}
.boxTitle{
    padding: 0 20px;
    margin-bottom: 10px;
   
}
.h2Title{
    margin: 0px;
    font-size: 21px;
    font-weight: 700;
    font-family: "Amazon Ember",Arial,sans-serif;
}
.box{
    padding: 0 20px;
    display: flex;
    height: 374px;
    flex-direction: column;
}
.boxRowWrapper{
    background: #fff;
    margin-bottom: 38px;
    display: flex;
    height:50%;
}
.boxRowOne{
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}
.boxRowTwo{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.boxImages{
    padding: 0px 12px 0 12px;
    width: 106%;
    height: 162px;
}
.seeMoreWrapper{
    margin-bottom: 20px;
    padding: 0 20px;
}
.seeMore{
    color: #0066c0;
    cursor: pointer;
}
.itemName{
    font-size: 12px;
}